.Animations{     
    width: 100%;
    height: 100%;  
    list-style: none;    
    display: flex;
    flex-flow: rows wrap;
    align-items: center;
    justify-content: center;    
    flex-wrap: wrap;
    overflow-y: scroll;
    border : none;
}
.Animations_Anim {   
    padding: 5px;
    width: 300px;
    height: 300px;
    margin: 40px;
    text-align: center;
    word-wrap: break-word;
    font: 45px "Times New Roman";  
    background-image: url("./anims.png");
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: column;
    box-shadow: 0 0 2px 2px lightblue;
    border-radius: 30px;    
}
.Animations_Anim:hover {
    box-shadow: 0 0 5px 5px lightblue;
}


