.Anim_sents_type{
    position: absolute;
    width: 1000px;
    height: 700px;
    left: 50%;
    top: 50%;
    margin-left: -500px;
    margin-top: -350px;
}
.Anim_sents_type__but__panel {
    position: absolute;
    width: 100%;
    height: 100px;
    top: 550px;  
    display: flex;
    justify-content: space-around;
    flex-direction: rows;
    align-items: center;
}
.Anim_sents_type__but {
    width: 230px;
    height: 70px;  
    font: 25px "Times New Roman";
    text-align: center;
    padding-top: 10px;
    border-radius: 10px;
}
.Anim_sents_type__but:hover {
    box-shadow: 0 0 10px 10px lightblue;
}

// 
.Anim_sents_type__rezPanel {
    position: absolute;
    width: 100%;
    height: 100px;
    top: 50px;  
    display: flex;
    justify-content: space-around;
    flex-direction: rows;
    align-items: center;   
}
.Anim_sents_type__rezPanel__circle {
    width: 90px;
    height: 90px;   
    border-radius: 45px;
    border: none;
    transition: 1s;
}
// 

.Anim_sents_type__sent {
    position: absolute;
    width: 100%;
    top: 15%;
    text-align: center;
    font: 55px "Times New Roman";   
}