.Principes{
    position: absolute;
    width: 1000px;
    height: 700px;
    left: 50%;
    top: 50%;
    margin-left: -500px;
    margin-top: -350px;
}
.Principes__but__panel {
    position: absolute;
    width: 100%;
    height: 100px;
    top: 550px;  
    display: flex;
    justify-content: space-around;
    flex-direction: rows;
    align-items: center;
}
.Principes__but {
    width: 350px;
    height: 50px;  
    font: 35px "Times New Roman";
    text-align: center;
    padding-top: 10px;
    border-radius: 10px;
}
.Principes__but:hover {
    box-shadow: 0 0 10px 10px lightblue;
}

// 
.Principes__rezPanel {
    position: absolute;
    width: 100%;
    height: 100px;
    top: 50px;  
    display: flex;
    justify-content: space-around;
    flex-direction: rows;
    align-items: center;   
}
.Principes__rezPanel__circle {
    width: 90px;
    height: 90px;   
    border-radius: 45px;
    border: none;
    transition: 1s;
}
// 

.Principes__word {
    position: absolute;
    width: 100%;
    top: 15%;
    text-align: center;
    font: 155px "Times New Roman";   
}